import React, { useEffect } from "react";
import { Profile } from "./Profile";
import { connect } from "react-redux";
import { onProfile, getProfileStatus, putProfileStatus, sendPhoto } from "../redux/ProfileReducer";
import { useParams } from 'react-router-dom';
import { withAuthNavigate } from "../hoc/withAuthNavigate";
import { compose } from "redux";
function withRouter(Children) {
    return (props) => {
        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}
const ProfileContainerComponent = (props) => {
    useEffect(() => {
        let profileId = props.match.params.profileId
        if (!profileId) {
            profileId = props.myUserId
        }
        props.onProfile(profileId)
        props.getProfileStatus(profileId)
    }, [props.match.params.profileId, props])

    return (<Profile
        {...props} profile={props.profile} profileId={props.match.params.profileId}
        status={props.status} putProfileStatus={props.putProfileStatus} sendPhoto={props.sendPhoto} />)
}
let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    myUserId: state.auth.userId,
    isAuth: state.auth.isAuth,
})

export const ProfileContainer = compose(
    connect(mapStateToProps, { onProfile, getProfileStatus, putProfileStatus, sendPhoto }),
    withRouter,
    withAuthNavigate
)(ProfileContainerComponent)