import React from "react";
import { ProfileStatusWithHooks } from "./ProfileStatusWithHooks";
export const Profile = (props) => {
    let nullUserPhoto = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTK11Cw35B_qnaEhLud9e2jBqYfaouUMuWdTdRvV94r7A&s"
    const onPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.sendPhoto(e.target.files[0])
        }
    }
    return (
        <>
            {props.profile.photos === undefined
                ? <img src={nullUserPhoto} alt="" />
                : <img src={props.profile.photos.small || nullUserPhoto} alt="" />}
            {props.profileId === undefined && <input type="file" name="photo" onChange={onPhotoSelected} />}
            <div>{props.profile.fullName}</div>
            <ProfileStatusWithHooks profileId={props.profileId} status={props.status} putProfileStatus={props.putProfileStatus} />
            <div>{props.profile.mail}</div>
        </>
    )
}